import server from '../../server.js';
export default {
	data() {
		return {
			TabCur:0,
			msgList:[]
		}
	},
	onLoad:function(){
		uni.showLoading({
			title: '加载中'
		});
		uni.request({
			method:'GET',
			url: "https://api.angeli.top/user.php?type=getMynotReadGZ",
			data: {
				
			},
			header: {
				'content-type': 'application/x-www-form-urlencoded',
				'Cookie':server.cookie
			},
			success: (res) => {
				console.log(res)
				if(res.data.code=="1"){
					this.msgList=res.data.data
					this.markMsg()
				}else{
					uni.showToast({
						title: "暂无新粉丝通知",
						position:'bottom',
						icon:'none'
					})
				}
				
			},
			complete() {
				uni.hideLoading();
			}
		});
	},
	methods: {
		tabSelect(e) {
			this.TabCur = e
		},
		guanzhu:function(index,uid){
			uni.request({
				method:'GET',
				url: 'https://api.angeli.top/user.php?type=gzORungz', //仅为示例，并非真实接口地址。
				data: {
					uid: uid,
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded',
					'Cookie':server.cookie
				},
				success: (res) => {
					uni.showToast({
						title: res.data.msg,
						position:'bottom',
						icon:'none'
					});
					if(res.data.msg=='关注成功'){
						
						this.msgList.isGZ=true;
					}else{
						this.msgList.isGZ=false;
					}
				}
			}); 
		},
		getbieren:function(e){
			uni.navigateTo({
				url: '../i/bieren?auid='+e
			})
		},
		getPostInfo:function(e){
			uni.navigateTo({
				url: '../postinfo/postinfo?id='+e
			});
		},
		markMsg:function(){
			uni.request({
				method:'GET',
				url: "https://api.angeli.top/user.php?type=mark&class=fans", //请求标记已读消息
				data: {
					
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded',
					'Cookie':server.cookie
				},
				success: (res) => {
					console.log(res)
					if(res.data.code=="1"){
						console.log('已将信息标记为已读')
					}else{
						console.log('标记已读失败！')
					}
					
				},
				complete() {
					
				}
			});
		}
	},
	onBackPress:function(){
		console.log('返回')
		uni.request({
			method:'GET',
			url: "https://api.angeli.top/user.php?type=mark&class=fans", //请求标记已读消息
			data: {
				
			},
			header: {
				'content-type': 'application/x-www-form-urlencoded',
				'Cookie':server.cookie
			},
			success: (res) => {
				console.log(res)
				if(res.data.code=="1"){
					console.log('已将信息标记为已读')
				}else{
					console.log('标记已读失败！')
				}
				
			},
			complete() {
				
			}
		});
	}
}
